  object-fit: cover;
}

/* scroll main */

.main::-webkit-scrollbar {
  width: 4px;
}

.main::-webkit-scrollbar-track {
  background: #1a191f;
}

.main::-webkit-scrollbar-thumb {
  background: #0FA;
  border-radius: 16px;
