.scores-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  column-gap: 8px;
}

.score-container,
.best-container {
  position: relative;
  display: inline-block;
  background: #bbada0;
  padding: 10px 28px;
  font-size: 15px;
  height: 25px;
  line-height: 47px;
  font-weight: 600;
  border: none;
  color: #000;
  margin-top: 0px;
  text-align: center;
  color: #fff;
  border-radius: 6px;
  background: var(--1, linear-gradient(135deg, #0FA 0%, #4579F5 53.01%, #9C42F5 99.83%));
  box-shadow: 0px 8px 8px -4px rgba(69, 121, 245, 0.12), 0px 16px 24px 0px rgba(69, 121, 245, 0.24), 0px 2px 4px -1px rgba(27, 10, 82, 0.12), 0px 0px 1px 0px rgba(69, 121, 245, 0.24);
  opacity: 0.9;
  border-radius: 8px;
}

.score-container:after,
.best-container:after,
.game-played:after {
  position: absolute;
  width: 100%;
  top: 10px;
  left: 0;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 800;
  line-height: 9px;
  text-align: center;
  color: #eee4da;
  opacity: 0.9;
}

.score-container .score-addition,
.best-container .score-addition {
  position: absolute;
  right: 30px;
  font-size: 17px;
  line-height: 18px;
  font-weight: 800;
  color: #fff;
  z-index: 100;
  -webkit-animation: move-up 600ms ease-in;
  -moz-animation: move-up 600ms ease-in;
  animation: move-up 600ms ease-in;
  -webkit-animation-fill-mode: both;
  -moz-animation-fill-mode: both;
  animation-fill-mode: both;
}

.score-container:after {
  content: "Score";
}

.best-container:after {
